$textcolor: rgba(210, 176, 210, 0.66);

@font-face {
    font-family: "Century";
    src: url("century_gothic_std_regular.otf") format("opentype");
}


body {
    font-family: Century,Arial,sans-serif;
    background-color: rgba(0, 0, 0, 0);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0;
}
#fullscreen {
    /** Fullscreen Background image:
    https://css-tricks.com/perfect-full-page-background-image/#aa-css-only-technique-1
     */
    top: 0;
    left: 0;
    min-height: 100%;
    min-width: 1024px;
    width: auto;
    height: 100%;
    position: fixed;
    display: block;
    z-index: -999;
    @media (min-width: 1084px) {
        width: 100%;
        height: auto;
    }
}
#avatar {
    height: 180px;
    border-radius: 100%;
    border: 3px solid $textcolor;
    margin-bottom: 40px;
}
h1 {
    color: $textcolor;
    font-weight: normal;
    letter-spacing: 0.0em;
    font-size: 1.3em;
    //font-family: Helvetica,Arial,sans-serif;
}
h3 {
    color: $textcolor;
    padding: 10px;
    font-weight: normal;
    font-size: 1em;
}
.content {
    padding-top: 80px;
    background-color: rgba(0, 0, 0, 0);
    text-align: center;
}
ul {
    list-style: none;
    padding: 0;
    text-align: center;
}
li {
    border-radius: 100%;
    border: 2px transparent;
    color: $textcolor;
    margin: 8px;
    display: inline-block;
    list-style: none;
    padding: 1px;
}
li:hover {
    color: $textcolor;
    //text-decoration: none;
    -webkit-transition: 500ms;
    -moz-transition: 500ms;
    transition: 500ms;
}
a {
    color: white;
    text-decoration: none;
}


.social-link > * {
    vertical-align: middle;
    margin:0;
}
.social-icon{
    display: none;
}
.social-network {
    display: inline-block;
    color: $textcolor;
    font-size: 1em;
    text-decoration: underline;
}

@media (max-width: 450px) {
    ul {
        text-align: center;
    }

    li {
        border-radius: 0;
        border: none;
        color: $textcolor;
        display: block;
        list-style: none;
        margin: 10px 0;
        text-align: center;
        height: auto;
        width: auto;
    }

    li:hover {
        background-color: transparent;
        transition: none;
    }

    a {
        text-align: justify;
    }

    a:hover {
        -webkit-transition: 500ms;
        -moz-transition: 500ms;
        transition: 500ms;
        background-color: rgba(0, 0, 0, 0.5);
        color: $textcolor;
        text-decoration: none;
    }

    .social-icon {
        display: none;
    }
    .social-network {
        display: inline-block;
    }
}
